<template>
    <div class="wrapper">
        <img v-bind:src="actualURL" />
        <div class="content">
            <h2>{{project.title}}</h2>
            <div class="language-style">
                <h3>Technologies:</h3>
                <div v-for="(language, index) in project.languages" v-bind:key="index" >
                    {{language}}
                </div>
            </div>
            <div v-for="(desc, index) in project.desc" :key="index">
                <p>{{desc}}</p>
            </div>
            <div class="link">
                <a :href="project.goToLink" target="_blank" rel="noopener noreferrer">{{getGoToText}}</a>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            publicPath: process.env.BASE_URL
        }
    },
    props: {
        project: Object,
    },
    computed: {
        actualURL() {
            return this.project.imgURL ? this.project.imgURL : "https://via.placeholder.com/250x150"
        },

        getGoToText() {
            if(this.project.goToLink == "#"){
                return "WIP"
            }else{
                return "Go To Project"
            }
        }
    }
}
</script>

<style scoped>
    .wrapper{
        border: 1px solid black;
        background: #FFF;
        color: rgb(21, 23, 51);
        width: 350px;
        padding: 15px 0px;
        margin: 25px 0px;
        border-radius: 15px;
        box-shadow: 1px 1px 5px black;

        transition: all 0.5s;
        transform: scale(1,1,1);
        z-index: 1;
    }

    .wrapper:hover{
        transition: all 0.5s;

        box-shadow: 10px 10px 20px black;
        transform: scale(1.3,1.3);
        z-index: 2;
        font-size: 0.9em;
        
    }

    .wrapper img{
        max-height: 200px;
        max-width: 90%;
    }

    .content{
        width: 90%;
        position: relative;
        padding-bottom: 75px;
    }

    p h3{
        padding: 0px 50px;
    }

    .language-style{
        transform: translateY(-15px);
    }

    .language-style h3{
        margin: 0px;
    }

    .language-style div{
        display: inline-block;
        padding: 3px 10px;

        margin: 5px;

        background: #CCC;
        box-shadow: 1px 1px 2px black;
        border: 1px solid #AAA;
        border-radius: 5px;
    }

    span{
        padding: 15px;
    }

    .content .link{
        position: absolute;
        bottom: 0;
        left: 50%;
        width: max-content;

        border-top: 1px solid;

        padding: 5px 40px 0px 40px;

        transform: translateX(-50%);

        cursor: pointer;
    }

    .link a{
        text-decoration: none;
        color: rgb(18, 69, 136);
        font-weight: bold;
        font-size: 1.3em;
    }

    .link a:hover{
        color: rgb(40, 111, 204);
    }
</style>
