<template>
    <div class="wrapper">
        <ProjectItem 
            v-bind:key="project.id" 
            v-for="project in matchingProjects" 
            v-bind:project="project"
        />
    </div>
</template>

<script>
import ProjectItem from "./ProjectItem"

export default {
    components:{
        ProjectItem
    },
    props: {
        items: Array,
        filters: {
            default: ["html", "c#", "python", "react", "vuejs", "css", "javascript", "android"],
            type: Array
        }
    },
    computed:{
        matchingProjects: function(){

            if(this.filters.length == 0){
                return this.items
            }

            let newArr = []

            for(var i = 0; i < this.items.length; i++){
                if(this.items[i].languages.some(r => this.filters.indexOf(r.toLowerCase()) >= 0)){
                    newArr.push(this.items[i])
                }
            }
            return newArr
        }
    }
}
</script>

<style scoped>
    .wrapper{
        display: flex;
        justify-content: space-around;
        flex-flow: row wrap;
        
    }
</style>
