<template>
    <div>
        <div class="title" id="Projects">
            <div class="border-bottom">&nbsp;</div>
            Projects
        </div>
        <FilterProjects v-on:filterChange="filterChanged" />
        <div class="project-area">
            <ProjectList v-bind:items="items" v-bind:filters="filters"/>
        </div>
    </div>
</template>

<script>
import FilterProjects from "./FilterProjects"
import ProjectList from "./ProjectList"
import Items from "../scripts/Projects.js"

export default {
    components: {
        FilterProjects,
        ProjectList
    },
    data: function() {
        return {
            filters: ["html", "c#", "python", "react", "vuejs", "css", "javascript", "android"],
            items: Items
        }
    },
    methods:{
        filterChanged(e){
            this.filters = e
        }
    }
}
</script>

<style scoped>
    .project-area{
        width: 70%;
        margin: 0 auto;
    }

    .title{
        position: relative;
        font-size: 300%;
        font-weight: 900;
        text-align: center;
        text-shadow: 0px 0px 2px rgb(45, 30, 129);
        color: #17eebc;
    }

    .border-bottom{
        border-bottom: 1px solid rgb(59, 114, 187);
        position: absolute;
        left: 40%;
        right: 40%;
    }
</style>
