<template>
    <div>
        <div class="selection-wrapper">
            <div class="styled-button blue semi-square" @click="function(){showFilters = !showFilters}">
                <input type="button" value="Filter" />
            </div>
        </div>
        <transition name="grow">
            <div v-if="showFilters" class="filter-styles">
                <FilterItem 
                v-bind:key="item.id" 
                v-for="item in FilterItems" 
                v-bind:text="item.id" 
                v-bind:enabled="item.enabled"
                v-on:filterItemChanged="adjustFilters" />
            </div>
        </transition>
    </div>
    
</template>

<script>
import FilterItem from "./FilterItem"

export default {
    components: {
        FilterItem
    },
    data(){
        return{
            filter: "all",
            showFilters: false,
            FilterItems: [
                {id:"HTML", enabled:false},
                {id:"C#", enabled:false},
                {id:"Python", enabled:false},
                {id:"React", enabled:false},
                {id:"VueJs", enabled:false},
                {id:"CSS", enabled:false},
                {id:"Javascript", enabled:false},
                {id:"Android", enabled:false}
            ]
        }
    },
    mounted(){
        
    },
    methods:{
        onChange(event){
            this.$emit("filterChange", this.filter)
        },
        adjustFilters(e){
            for(var i = 0; i < this.FilterItems.length; i++){
                if(this.FilterItems[i].id === e){
                    this.FilterItems[i].enabled = !this.FilterItems[i].enabled

                    let returnArr = []

                    for(var i = 0; i < this.FilterItems.length; i++){
                        if(this.FilterItems[i].enabled){
                            returnArr.push(this.FilterItems[i].id.toLowerCase().trim())
                        }
                    }

                    this.$emit("filterChange", returnArr)
                }
            }
        }
    }
}
</script>

<style scoped>
    *{
        font-size: inherit;
    }

    .selection-wrapper{
        width: 90%;
        margin: 0 auto;
        padding: 15px 0px;

        display: flex;
        flex-flow: row;
        justify-content: space-around;
    }

    .styled-select{
        background: url(http://i62.tinypic.com/15xvbd5.png) no-repeat 96% 5px;
        overflow: hidden;
        color: #fff;
        padding: 5px 10px;
        border: 2px solid #CCC;
    }

    .styled-select select{
        -moz-appearance: none;
        background: transparent;
        border: none;
        height: 30px;
        width: 200px;
        text-align: center;
        color: #fff;
    }

    .styled-button{
        background: url(http://i62.tinypic.com/15xvbd5.png) no-repeat 96% 5px;
        overflow: hidden;
        padding: 5px 10px;
        border: 2px solid #CCC;

        cursor: pointer;
    }

    .styled-button input{
        background: transparent;
        border: none;
        color: #fff;
        width: 200px;

        cursor: inherit;
    }

    .semi-square{
        border-radius: 8px;
    }

    .blue{
        background-color: dodgerblue;
    }

    .filter-styles{
        background: dodgerblue;
        color: #fff;
        height: 100px;
        width: 65%;
        margin: 0 auto;
        border-radius: 20px;
        padding: 15px 0px;

        display: flex;
        flex-flow: column wrap;
        justify-content: space-around;
    }

    .filter-styles input{
        margin-right: 10px;
    }

    .filter-styles div{
        text-align: left;
        margin-left: 100px;
    }

    .grow-enter-active, .grow-leave-active{
        transition: all 1s;
        max-height: 100px;
        min-height: 50px;
        height: 100px;
    }

    .grow-enter, .grow-leave-to{
        opacity: 0;
        max-height: 0px;
        height: 0px;
        min-height: 0px;
        padding: 0px;
    }

    .grow-enter-active > *{
        animation: fade-in 1.42s;
    }

    .grow-leave-active > *{
        animation: fade-in 0.16s reverse;
    }

    .grow-leave-to > *{
        opacity: 0;
    }

    @keyframes fade-in {
        0%{
            opacity: 0;
        }
        50%{
            opacity: 0;
        }
        100%{
            opacity: 1;
        }
    }
    

</style>
