<template>
    <div>
        <div class="title" id="Skills">
            <div class="border-bottom">&nbsp;</div>
            Tech Stack
        </div>
        <div class="flex-row">
            <div v-for="(type, index) in skillTypes" :key="index">
                <div class="flex-col">
                    <span class="heading">{{type[0].type}}</span>
                    <div v-for="(skill, index2) in type" :key="index2">
                        <img :src="getSrc(skill.src)" :alt="skill.src" /><br />
                        <div class="scale-text">{{skill.name}}</div>
                    </div>
                </div>
            </div>
        </div>        
    </div>
</template>

<script>
export default {
    data: function() {
        return {
            skills: [
                { name: "", src: "Photoshop", type: "Applications"},
                { name: "Krita", src: "Krita", type: "Applications"},
                { name: "SQL", src: "SQL", type: "Databases"},
                { name: "", src: "MongoDB", type: "Databases"},
                { name: "", src: "Express.js", type: "Frameworks"},
                { name: "Mongoose.js", src: "Mongoose.js", type: "Libraries"},
                { name: "", src: "Node.js", type: "Frameworks"},
                { name: "VS Code", src: "Visual Studio Code", type: "Applications"},
                { name: "Github", src: "GitHub", type: "Version Controls"},
                { name: "React", src: "React", type: "Libraries"},
                { name: "Vue JS", src: "Vue", type: "Frameworks"},
                { name: "", src: "Javascript", type: "Languages"},
                { name: "", src: "CSharp", type: "Languages"},
                { name: "Python", src: "Python", type: "Languages"},
            ]
        }
    },
    methods:{
        getSrc(val){
            return './Images/Icons/' + val + '.png' || require('../assets/Icons/' + val + '.png')
        }
    },
    computed:{
        skillTypes(){
            let apps = this.skills.filter(skill => skill.type == "Applications")
            let database = this.skills.filter(skill => skill.type == "Databases")
            let library = this.skills.filter(skill => skill.type == "Libraries")
            let framework = this.skills.filter(skill => skill.type == "Frameworks")
            let version = this.skills.filter(skill => skill.type == "Version Controls")
            let language = this.skills.filter(skill => skill.type == "Languages")

            return [apps, database, library, framework, version, language]
        }
    }
}
</script>

<style scoped>
    .title{
        position: relative;
        font-size: 300%;
        font-weight: 900;
        text-align: center;
        text-shadow: 0px 0px 2px rgb(45, 30, 129);
        margin-bottom: 15px;
        color: #17eebc;
    }

    .border-bottom{
        border-bottom: 1px solid rgb(59, 114, 187);
        position: absolute;
        left: 40%;
        right: 40%;
    }

    .flex-row{
        display: flex;
        flex-flow: row wrap;
        justify-content: space-around;

        width: 80%;
        margin: 0 auto;
        text-align: center;
    }

    .flex-row > div{
        margin: 10px 10px;
    }

    .flex-col{
        display: flex;
        flex-flow: column wrap;
        justify-content: space-around;
        justify-items: center;
        width: 80%;

        margin: 0 auto;

        color: rgb(41, 128, 150);
        font-weight: bold;
    }

    .flex-col > div{
        margin: 16px 0px;
        height: 125px;
        max-width: 125px;
        width: 100%;
    }

    .scale-text{
        font-size: 1.6vh;
    }

    .heading{
        color: rgb(41, 128, 150);
        font-size: 1.85vh;
        font-weight: 600;
        text-align: center;
    }

    @media only screen and (max-width: 600px) {
        .flex-col div{
            margin-top: 5px;
            height: 100px;
            max-width: 100px;
            width: 100%;            
        }

        .scale-text{
            font-size: 2.6vh;
        }

        .heading{
            color: rgb(41, 128, 150);
            font-size: 3vh;
            font-weight: 600;
            text-align: center;
        }
    }

    img{
        width: 100%;
        height: auto;
    }

    
</style>